<template>
  <div id="app">
    <yd-layout>
      <Vhome v-if="this.$route.path=='/home'"></Vhome>
      <router-view/>
      <vfooter></vfooter>
    </yd-layout>
  </div>
</template>

<script type="text/ecmascript-6">
import {GridsGroup, GridsItem} from 'vue-ydui'

import Vfooter from "@/components/vfooter"
import Vheader from "@/components/vheader"
import Vhome from "@/page/home/home"

export default {
  name: 'App',
  components: {
    Vfooter,
    GridsGroup,
    GridsItem,
    Vhome

  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.title{font-size: 18px; line-height: 30px}
</style>



