import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import fastclick from 'fastclick'
import YDUI from 'vue-ydui'; /* 相当于import YDUI from 'vue-ydui/ydui.rem.js' */

import axios from 'axios';
import VueAxios from 'vue-axios'


fastclick.attach(document.body)

import 'vue-ydui/dist/ydui.px.css'

Vue.use(VueAxios, axios)
Vue.use(YDUI)
Vue.use(VueRouter)
Vue.config.productionTip = false

const router = new VueRouter({
  routes: [
      {
          path: '/login',
          component: resolve => require(['./page/login/login.vue'], resolve) /* <== 主要代码是这 */
      },
      {
          path: '/regsiter',
          component: resolve => require(['./page/regsiter/register.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/setting',
        component: resolve => require(['./page/setting/setting.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/roomie',
        component: resolve => require(['./page/roomie/roomie.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/upload',
        component: resolve => require(['./page/upload/upload.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/map',
        component: resolve => require(['./page/map/map.vue'], resolve) /* <== 主要代码是这 */
      }
  ]
});




/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



